<template>
  <div
    ref="cardRef"
    class="holographic-card group relative cursor-pointer overflow-hidden rounded-xl"
    :class="[
      cardClasses,
      {
        'holographic-active': isHovered,
        'animate-pulse': loading,
      },
    ]"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave"
    @mousemove="handleMouseMove"
    @click="handleClick"
  >
    <!-- Background holographique -->
    <div
      ref="hologramRef"
      class="absolute inset-0 opacity-0 transition-opacity duration-500"
      :style="holographicStyle"
    ></div>

    <!-- Effet de brillance -->
    <div
      ref="shineRef"
      class="absolute inset-0 opacity-0 transition-all duration-700"
      :style="shineStyle"
    ></div>

    <!-- Particules flottantes -->
    <div v-if="particles" class="pointer-events-none absolute inset-0">
      <div
        v-for="particle in particleElements"
        :key="particle.id"
        class="absolute h-1 w-1 animate-float rounded-full bg-current"
        :style="particle.style"
      ></div>
    </div>

    <!-- Bordure animée -->
    <div
      class="absolute inset-0 -z-10 rounded-xl border-2 border-transparent bg-gradient-to-r from-blue-500 via-purple-500 to-pink-500 opacity-0 transition-opacity duration-300 group-hover:opacity-50"
    ></div>

    <!-- Contenu principal -->
    <div class="relative z-10 flex h-full flex-col items-center justify-center p-6 text-center">
      <!-- Icône avec effet holographique -->
      <div
        ref="iconRef"
        class="mb-4 transform transition-all duration-300 group-hover:scale-110"
        :style="iconStyle"
      >
        <slot name="icon">
          <i :class="icon" class="text-4xl" :style="`color: ${color}`"></i>
        </slot>
      </div>

      <!-- Titre -->
      <h3
        ref="titleRef"
        class="mb-2 transform text-lg font-bold transition-all duration-300 group-hover:text-white"
        :class="titleClasses"
      >
        <slot name="title">{{ title }}</slot>
      </h3>

      <!-- Description -->
      <p
        v-if="description"
        class="mb-3 transform text-sm opacity-80 transition-all duration-300 group-hover:opacity-100"
        :class="descriptionClasses"
      >
        {{ description }}
      </p>

      <!-- Badge de niveau -->
      <div v-if="level" class="transform transition-all duration-300 group-hover:scale-105">
        <UiBadge :variant="levelVariant" size="sm" class="holographic-badge">
          {{ level }}
        </UiBadge>
      </div>

      <!-- Contenu personnalisé -->
      <div class="mt-auto">
        <slot></slot>
      </div>
    </div>

    <!-- Effet de ripple au clic -->
    <div
      v-if="showRipple"
      ref="rippleRef"
      class="animate-ripple pointer-events-none absolute rounded-full"
      :style="rippleStyle"
    ></div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useAdvancedAnimations } from '@/composables/useAdvancedAnimations'
import UiBadge from './UiBadge.vue'

interface ParticleElement {
  id: number
  style: Record<string, string>
}

interface Props {
  title?: string
  description?: string
  icon?: string
  color?: string
  level?: string
  variant?: 'primary' | 'secondary' | 'success' | 'warning' | 'danger'
  size?: 'sm' | 'md' | 'lg'
  particles?: boolean
  loading?: boolean
  glowIntensity?: number
  hoverScale?: number
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'primary',
  size: 'md',
  particles: true,
  glowIntensity: 0.3,
  hoverScale: 1.05,
})

const emit = defineEmits<{
  click: []
  hover: [isHovered: boolean]
}>()

// Refs
const cardRef = ref<HTMLElement>()
const hologramRef = ref<HTMLElement>()
const shineRef = ref<HTMLElement>()
const iconRef = ref<HTMLElement>()
const titleRef = ref<HTMLElement>()
const rippleRef = ref<HTMLElement>()

// État
const isHovered = ref(false)
const mousePosition = ref({ x: 0, y: 0 })
const showRipple = ref(false)
const particleElements = ref<ParticleElement[]>([])

// Animation composable
const { createRippleEffect, holographicEffect } = useAdvancedAnimations()

// Classes calculées
const cardClasses = computed(() => [
  'holographic-card',
  `holographic-${props.variant}`,
  `holographic-${props.size}`,
  {
    'h-32': props.size === 'sm',
    'h-40': props.size === 'md',
    'h-48': props.size === 'lg',
  },
])

const titleClasses = computed(() => ['text-gray-900 dark:text-white'])

const descriptionClasses = computed(() => ['text-gray-600 dark:text-gray-300'])

const levelVariant = computed(() => {
  switch (props.level?.toLowerCase()) {
    case 'expert':
      return 'success'
    case 'avancé':
      return 'primary'
    case 'intermédiaire':
      return 'warning'
    case 'débutant':
      return 'secondary'
    default:
      return 'default'
  }
})

// Styles dynamiques
const holographicStyle = computed(() => {
  if (!isHovered.value) return { opacity: '0' }

  const { x, y } = mousePosition.value
  return {
    background: `radial-gradient(circle at ${x}% ${y}%, 
      rgba(59, 130, 246, 0.3), 
      rgba(147, 51, 234, 0.2), 
      rgba(236, 72, 153, 0.1),
      transparent 50%)`,
    opacity: props.glowIntensity.toString(),
  }
})

const shineStyle = computed(() => {
  if (!isHovered.value) return { opacity: '0' }

  const { x } = mousePosition.value
  return {
    background: `linear-gradient(135deg, 
      transparent ${Math.max(0, x - 20)}%, 
      rgba(255, 255, 255, 0.1) ${x}%, 
      transparent ${Math.min(100, x + 20)}%)`,
    opacity: '1',
  }
})

const iconStyle = computed(() => ({
  filter: isHovered.value ? `drop-shadow(0 0 10px ${props.color}) brightness(1.2)` : 'none',
}))

const rippleStyle = ref({})

// Gestion des particules
const generateParticles = () => {
  if (!props.particles) return

  const particles: ParticleElement[] = []
  for (let i = 0; i < 6; i++) {
    particles.push({
      id: i,
      style: {
        left: `${Math.random() * 100}%`,
        top: `${Math.random() * 100}%`,
        animationDelay: `${Math.random() * 2}s`,
        animationDuration: `${2 + Math.random() * 2}s`,
        color: props.color || '#3b82f6',
      },
    })
  }
  particleElements.value = particles
}

// Gestionnaires d'événements
const handleMouseEnter = () => {
  isHovered.value = true
  emit('hover', true)

  if (hologramRef.value) {
    holographicEffect(hologramRef.value)
  }
}

const handleMouseLeave = () => {
  isHovered.value = false
  emit('hover', false)
  mousePosition.value = { x: 0, y: 0 }
}

const handleMouseMove = (event: MouseEvent) => {
  if (!cardRef.value) return

  const rect = cardRef.value.getBoundingClientRect()
  const x = ((event.clientX - rect.left) / rect.width) * 100
  const y = ((event.clientY - rect.top) / rect.height) * 100

  mousePosition.value = { x, y }
}

const handleClick = async (event: MouseEvent) => {
  if (!cardRef.value) return

  // Effet ripple
  showRipple.value = true
  const rippleResult = createRippleEffect(event, cardRef.value)

  if (rippleRef.value && rippleResult) {
    Object.assign(rippleRef.value.style, rippleResult)
  }

  // Nettoyer le ripple après l'animation
  setTimeout(() => {
    showRipple.value = false
  }, 600)

  emit('click')
}

// Lifecycle
onMounted(() => {
  generateParticles()

  // Animation d'entrée
  if (cardRef.value) {
    cardRef.value.style.opacity = '0'
    cardRef.value.style.transform = 'translateY(20px)'

    setTimeout(() => {
      if (cardRef.value) {
        cardRef.value.style.transition = 'all 0.6s ease-out'
        cardRef.value.style.opacity = '1'
        cardRef.value.style.transform = 'translateY(0)'
      }
    }, 100)
  }
})

onUnmounted(() => {
  // Cleanup si nécessaire
})
</script>

<style scoped>
.holographic-card {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(8px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
}

.dark .holographic-card {
  background: rgba(17, 24, 39, 0.8);
}

.holographic-card:hover {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  transform: translateY(-8px) rotateX(5deg);
}

.holographic-active {
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.3);
}

/* Variantes de couleur */
.holographic-primary:hover {
  box-shadow: 0 25px 50px -12px rgba(59, 130, 246, 0.2);
}

.holographic-secondary:hover {
  box-shadow: 0 25px 50px -12px rgba(107, 114, 128, 0.2);
}

.holographic-success:hover {
  box-shadow: 0 25px 50px -12px rgba(34, 197, 94, 0.2);
}

.holographic-warning:hover {
  box-shadow: 0 25px 50px -12px rgba(234, 179, 8, 0.2);
}

.holographic-danger:hover {
  box-shadow: 0 25px 50px -12px rgba(239, 68, 68, 0.2);
}

/* Badge holographique */
.holographic-badge {
  backdrop-filter: blur(8px);
  background: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.dark .holographic-badge {
  background: rgba(31, 41, 55, 0.2);
}

.holographic-card:hover .holographic-badge {
  background: rgba(255, 255, 255, 0.3);
}

.dark .holographic-card:hover .holographic-badge {
  background: rgba(31, 41, 55, 0.3);
}

/* Animation des particules */
@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-10px) rotate(180deg);
    opacity: 1;
  }
}

.animate-float {
  animation: float linear infinite;
}

/* Animation ripple */
@keyframes ripple {
  0% {
    transform: scale(0);
    opacity: 0.6;
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}

.animate-ripple {
  animation: ripple 0.6s ease-out;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3), transparent);
}

/* Effet de lueur douce */
.holographic-card::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: inherit;
  padding: 2px;
  background: linear-gradient(45deg, transparent, rgba(59, 130, 246, 0.1), transparent);
  mask:
    linear-gradient(#fff 0 0) content-box,
    linear-gradient(#fff 0 0);
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.holographic-card:hover::before {
  opacity: 1;
}
</style>
